import { NavLink} from "react-router-dom";
import React, {useState} from 'react';
import axios from 'axios';
import { resetUserSession } from "./service/AuthService";

const applyLoanUrl = 'https://u9nyrvkbu2.execute-api.ap-south-1.amazonaws.com/1st/applyloan';

const ApplyLoan = (props) => {
    const [username, setUserName] = useState('');
    const [loanType, setLoanType] = useState('');
    const [amount, setAmount] = useState('');
    const [date, setDate] = useState('');
    const [rateOfInterest, setRateOfInterest] = useState('');
    const [durationOfLoan, setDurationOfLoan] = useState('');
    const [message, setMessage] = useState(null);
    const token = sessionStorage.token;
    const logoutHandler = (event)=> {
        resetUserSession();
        props.history.push('/login');
    }

    const submitHandler = (event) =>{
        event.preventDefault();
        if(username.trim() === '' || loanType.trim() === '' || amount.trim() === '' || date.trim() === '' || rateOfInterest.trim() === '' || durationOfLoan.trim() === '') {
            setMessage('All fields are required!!!');
            return;
        }
        setMessage(null);
        const requestConfig = {
            headers: {
                'x-api-key': 'XP2lSkpg1G80lh0oHMHIe99WMVzoGL9Q4ToSU4tN'
            }
        }
        const requestBody = {
            username: username,
            loanType: loanType,
            amount: amount,
            date: date,
            rateOfInterest: rateOfInterest,
            durationOfLoan: durationOfLoan,
            token: JSON.parse(token)
        }
        //console.log(requestBody);
        axios.post(applyLoanUrl, requestBody, requestConfig).then(response =>{
            setMessage('Applied Successfully !!');
            sessionStorage.setItem('loan',JSON.stringify(response.data.user));
            props.history.push('/dashboard');
        }).catch(error=>{
            setMessage(error.response.data.message);
        })

    }

    return (
        <div>
            <div className="header">
                <h1>Bank Of Montreal</h1>
                <div Style="align-self:center;">
                    <NavLink activeClassName="active" to="/dashboard">Home</NavLink>
                    <NavLink activeClassName="active" to="/login" onClick={logoutHandler}>logout</NavLink>
                </div>
            </div>
            <div className="registerForm">
                <div>
                    <form onSubmit={submitHandler}>
                        <h4>Apply Loan Now !!!</h4>
                        Username    : <input type="text" value={username} onChange={event=> setUserName(event.target.value)}/><br/>
                        Loan Type    : <input type="text" value={loanType} onChange={event=> setLoanType(event.target.value)}/><br/>
                        Amount     : <input type="number" value={amount} onChange={event=> setAmount(event.target.value)}/><br/>
                        Rate of Interest : <input type="text" value={rateOfInterest} onChange={event=> setRateOfInterest(event.target.value)}/><br/>
                        Durration Of Loan(years) : <input type="number" value={durationOfLoan} onChange={event=> setDurationOfLoan(event.target.value)}/><br/>
                        Date : <input type="date" value={date} onChange={event=> setDate(event.target.value)}/><br/>
                        <br/>
                        <input type="submit" value="Apply"  Style="font-size: 1em; width: auto; padding: 8px; border-radius: 5px; color:blue;"/>
                    </form>
                    {message && <p className="message">{message}</p>}
                </div>
            </div>
        </div>
    )
}

export default ApplyLoan;