import { NavLink} from "react-router-dom";
import React, {useState} from 'react';
import axios from 'axios';
import { getUser,resetUserSession } from './service/AuthService';

const updateUrl = 'https://u9nyrvkbu2.execute-api.ap-south-1.amazonaws.com/1st/updateuser';

const Update = (props) => {
    const user = getUser();
    const token = sessionStorage.token;
    const [name, setName] = useState(user.name);
    const [email, setEmail] = useState(user.email);
    const [password, setPassword] = useState('');
    const [address, setAddress] = useState(user.address);
    const [state, setState] = useState(user.state);
    const [country, setCountry] = useState(user.country);
    const [pan, setPAN] = useState(user.pan);
    const [phone, setPhone] = useState(user.phone);
    const [dob, setDob] = useState(user.dob);
    const [acType, setAcType] = useState(user.acType);
    const [message, setMessage] = useState(null);

    const logoutHandler = (event)=> {
        resetUserSession();
        props.history.push('/login');
    }
    const submitHandler = (event) =>{
        event.preventDefault();
        if(name.trim() === '' || email.trim() === '' || password.trim() === '' || address.trim() === '' || state.trim() === '' || country.trim() === '' || pan.trim() === '' || phone.trim() === '' || dob.trim() === '' || acType.trim() === '') {
            setMessage('All fields are required!!!');
            return;
        }
        setMessage(null);
        const requestConfig = {
            headers: {
                'x-api-key': 'XP2lSkpg1G80lh0oHMHIe99WMVzoGL9Q4ToSU4tN'
            }
        }
        const requestBody = {
            name: name,
            email: email,
            username: user.username,
            password: password,
            address: address,
            state: state,
            country: country,
            pan: pan,
            phone: phone,
            dob: dob,
            acType: acType,
            token: JSON.parse(token)
        }
        axios.post(updateUrl, requestBody, requestConfig).then(response =>{
            setMessage('Applied Successfully !!');
            sessionStorage.setItem('user',JSON.stringify(response.data.user));
            props.history.push('/dashboard');
        }).catch(error=>{
            setMessage(error.response.data.message);
        })

    }

    return (
        <div>
            <div className="header">
                <h1>Bank Of Montreal</h1>
                <div Style="align-self:center;">
                    <NavLink activeClassName="active" to="/dashboard">Home</NavLink>
                    <NavLink activeClassName="active" to="/login" onClick={logoutHandler}>logout</NavLink>
                </div>
            </div>
            <div className="registerForm">
                <div>
                    <form onSubmit={submitHandler}>
                        <h4>Update personal Information</h4>
                        Name        : <input type="text" value={name} onChange={event=> setName(event.target.value)}/><br/>
                        Email       : <input type="email" value={email} onChange={event=> setEmail(event.target.value)}/><br/>
                        Password    : <input type="password" value={password} onChange={event=> setPassword(event.target.value)}/><br/>
                        Address     : <input type="text" value={address} onChange={event=> setAddress(event.target.value)}/><br/>
                        State       : <input type="text" value={state} onChange={event=> setState(event.target.value)}/><br/>
                        Country     : <input type="text" value={country} onChange={event=> setCountry(event.target.value)}/><br/>
                        PAN         : <input type="text" value={pan} onChange={event=> setPAN(event.target.value)}/><br/>
                        Phone       : <input type="number" value={phone} onChange={event=> setPhone(event.target.value)}/><br/>
                        DateOfBirth : <input type="date" value={dob} onChange={event=> setDob(event.target.value)}/><br/>
                        Account Type: <input type="text" value={acType} onChange={event=> setAcType(event.target.value)}/><br/>
                        <br/>
                        <input type="submit" value="Update" Style="font-size: 1em; width: auto; padding: 8px; border-radius: 5px; color:white; background-color:blue" />
                    </form>
                    {message && <p className="message">{message}</p>}
                </div>
            </div>
        </div>
    )
}

export default Update;