import React, {useState} from 'react';
import axios from 'axios';
import { NavLink} from "react-router-dom";

const registerUrl = 'https://u9nyrvkbu2.execute-api.ap-south-1.amazonaws.com/1st/signup';

const Register = () => {
    const [name, setName] = useState('');
    const [email, setEmail] = useState('');
    const [username, setUserName] = useState('');
    const [password, setPassword] = useState('');
    const [address, setAddress] = useState('');
    const [state, setState] = useState('');
    const [country, setCountry] = useState('');
    const [pan, setPAN] = useState('');
    const [phone, setPhone] = useState('');
    const [dob, setDob] = useState('');
    const [acType, setAcType] = useState('');
    const [message, setMessage] = useState(null);

    const submitHandler = (event) =>{
        event.preventDefault();
        if(name.trim() === '' || email.trim() === '' || username.trim() === '' || password.trim() === '' || address.trim() === '' || state.trim() === '' || country.trim() === '' || pan.trim() === '' || phone.trim() === '' || dob.trim() === '' || acType.trim() === '') {
            setMessage('All fields are required!!!');
            return;
        }
        setMessage(null);
        const requestConfig = {
            headers: {
                'x-api-key': 'XP2lSkpg1G80lh0oHMHIe99WMVzoGL9Q4ToSU4tN'
            }
        }
        const requestBody = {
            name: name,
            email: email,
            username: username,
            password: password,
            address: address,
            state: state,
            country: country,
            pan: pan,
            phone: phone,
            dob: dob,
            acType: acType
        }
        axios.post(registerUrl, requestBody, requestConfig).then(response =>{
            setMessage('Registration Successful !! Log in now to apply loan...');
        }).catch(error=>{
            setMessage(error.response.data.message);
        })

    }

    return (
        <div>
            <div className="header">
            <h1>Bank Of Montreal</h1>
            <NavLink activeClassName="active" to="/login">Login</NavLink>
            </div>
            <div className="registerForm">
                <form onSubmit={submitHandler}>
                    <h4>Register Yourself</h4>
                    Name        : <input type="text" value={name} onChange={event=> setName(event.target.value)}/><br/>
                    Email       : <input type="email" value={email} onChange={event=> setEmail(event.target.value)}/><br/>
                    Username    : <input type="text" value={username} onChange={event=> setUserName(event.target.value)}/><br/>
                    Password    : <input type="password" value={password} onChange={event=> setPassword(event.target.value)}/><br/>
                    Address     : <input type="text" value={address} onChange={event=> setAddress(event.target.value)}/><br/>
                    State       : <input type="text" value={state} onChange={event=> setState(event.target.value)}/><br/>
                    Country     : <input type="text" value={country} onChange={event=> setCountry(event.target.value)}/><br/>
                    PAN         : <input type="text" value={pan} onChange={event=> setPAN(event.target.value)}/><br/>
                    Phone       : <input type="number" value={phone} onChange={event=> setPhone(event.target.value)}/><br/>
                    DateOfBirth : <input type="date" value={dob} onChange={event=> setDob(event.target.value)}/><br/>
                    Account Type: <input type="text" value={acType} onChange={event=> setAcType(event.target.value)}/><br/>
                    <br/>
                    <input type="submit" value="Register" Style="font-size: 1em; width: auto; padding: 8px; border-radius: 5px;"/>
                </form>
                
            </div>
            {message && <p className="message">{message}</p>}
        </div>
    )
}

export default Register;