import { NavLink} from "react-router-dom";
import React, {useState} from 'react';
import axios from 'axios';
import { setUserSession } from './service/AuthService'
const loginAPIUrl = 'https://u9nyrvkbu2.execute-api.ap-south-1.amazonaws.com/1st/login';



const Login = (props) => {
  const [username, setUsername] = useState('');
  const [password, setPassword] = useState('');
  const [errorMessage, setErrorMessage] = useState(null);

  const submitHandler = (event) => {
    event.preventDefault();
    if (username.trim() === '' || password.trim() === '') {
      setErrorMessage('Both username and password are required');
      return;
    }
    setErrorMessage(null);
    const requestConfig = {
      headers: {
        'x-api-key': 'XP2lSkpg1G80lh0oHMHIe99WMVzoGL9Q4ToSU4tN'
      }
    }
    const requestBody = {
      username: username,
      password: password
    }

    axios.post(loginAPIUrl, requestBody, requestConfig).then((response) => {
      setUserSession(response.data.user.userDetails, response.data.loan.loanDetails, response.data.token);
      props.history.push('/Dashboard');
      //console.log(sessionStorage.user,sessionStorage.loan, sessionStorage.token);
    
    }).catch((error) => {
        setErrorMessage(error.response.data.message);
    })
  }

  return (
    <div>
    <div className="header">
        <h1>Bank Of Montreal</h1>
        <NavLink exact activeClassName="active" to="/register">Register</NavLink>
      
      {/* <NavLink activeClassName="active" to="/login">Login</NavLink> */}
    </div>
    <div className="registerForm">
      <form onSubmit={submitHandler} >
        <h2>Login Here !!!</h2>
        username: <input type="text" value={username} onChange={event => setUsername(event.target.value)} /> <br/>
        password: <input type="password" value={password} onChange={event => setPassword(event.target.value)} /> <br/><br/>
        <input type="submit" value="Login" Style="font-size: 1em; width: auto; padding: 8px; border-radius: 5px;"/>
      </form>
    </div>
    {errorMessage && <p className="message">{errorMessage}</p>}
    </div>
  )
}

export default Login;