import { NavLink} from "react-router-dom";
import React from 'react';
import { getUser, getLoan, resetUserSession } from './service/AuthService';



const Dashboard = (props) => {
    const user = getUser();
    const name = user !== 'undefined' && user ? user.name: '';
    const username = user !== 'undefined' && user ? user.username: '';
    const email = user !== 'undefined' && user ? user.email: '';
    const phone = user !== 'undefined' && user ? user.phone: '';
    const address = user !== 'undefined' && user ? user.address: '';
    const state = user !== 'undefined' && user ? user.state: '';
    const country = user !== 'undefined' && user ? user.country: '';
    const pan = user !== 'undefined' && user ? user.pan: '';
    const dob = user !== 'undefined' && user ? user.dob: '';
    const acType = user !== 'undefined' && user ? user.acType: '';

    const applyLoanHandler = (event) => {
        event.preventDefault();
        props.history.push('/ApplyLoan');
    }
    const logoutHandler = (event)=> {
        resetUserSession();
        props.history.push('/login');
    }
    const loan = sessionStorage.loan;
    if(loan === null || loan === 'undefined') {
        return (
            <div>
                <div className="header">
                <h1>Bank Of Montreal</h1>
                    <div Style="align-self:center;">
                        <NavLink activeClassName="active" to="/update">Update</NavLink>
                        <NavLink activeClassName="active" to="/login" onClick={logoutHandler}>logout</NavLink>
                    </div>
                </div>
                <div className="registerForm">
                    <div>
                        <h3>Dashboard</h3>
                        {username && <h5>Hii, {username}  </h5>}
                        <h4>Personal Information</h4>
                        Name : {name}<br/>
                        Email : {email}<br/>
                        Address : {address}<br/>
                        State : {state}<br/>
                        Country : {country}<br/>
                        PAN : {pan}<br/>
                        phone : {phone}<br/>
                        Date Of Birth : {dob}<br/>
                        Account : {acType}<br/>
                        <h4 Style="color: red;">You have not applied for any loan. Click on Apply to apply for the loan.</h4>
                        <input type = "button" value="Apply Loan" onClick={applyLoanHandler} Style="font-size: 1em; width: auto; padding: 8px; border-radius: 5px;"/><br/>
                    </div>
                </div>
            </div>
        )
    }
    else {
        const loan = getLoan();
        const loanType = loan !== 'undefined' && loan ? loan.loanType: '';
        const amount = loan !== 'undefined' && loan ? loan.amount: '';
        const date = loan !== 'undefined' && loan ? loan.date: '';
        const rateOfInterest = loan !== 'undefined' && loan ? loan.rateOfInterest: '';
        const durationOfLoan = loan !== 'undefined' && loan ? loan.durationOfLoan: '';
        return (
            <div>
                <div className="header">
                <h1>Bank Of Montreal</h1>
                    <div Style="align-self:center;">
                        <NavLink activeClassName="active" to="/update">Update</NavLink>
                        <NavLink activeClassName="active" to="/login" onClick={logoutHandler}>logout</NavLink>
                    </div>
                </div>
                <div className="registerForm">
                    <div>
                        <h3>Dashboard</h3>
                        {username && <h5>Hii, {username}  </h5>}
                        <h4>Personal Information</h4>
                        Name : {name}<br/>
                        Email : {email}<br/>
                        Address : {address}<br/>
                        State : {state}<br/>
                        Country : {country}<br/>
                        PAN : {pan}<br/>
                        phone : {phone}<br/>
                        Date Of Birth : {dob}<br/>
                        Account : {acType}<br/>
                        <h4 Style="color: green;">You have already applied for the loan. Your application is under review..</h4>
                        Loan Type : {loanType}<br/>
                        Amount : {amount}<br/>
                        Date : {date} <br/>
                        Rate Of Interest : {rateOfInterest} <br/>
                        Duration Of Loan : {durationOfLoan}<br/>
                    </div>
                </div>
            </div>
        )
    }
}

export default Dashboard;