import {BrowserRouter, Switch} from "react-router-dom";
import Register from "./register";
import Login from "./login";
import Dashboard from "./dashboard";
import ApplyLoan from "./applyloan";
import Update from "./update";
import PrivateRoute from "./routes/PrivateRoute";
import PublicRoute from "./routes/PublicRoute";
import React, {useEffect} from "react";
import axios from "axios";
import { getUser, resetUserSession } from "./service/AuthService";
const verifyTokenUrl = 'https://u9nyrvkbu2.execute-api.ap-south-1.amazonaws.com/1st/verify';

function App() {
  useEffect(()=>{
    const token = sessionStorage.token;
    const user = getUser();
    if(token === 'undefined' || token === undefined || token === null || !token) {
      return;
    }
    const requestConfig = {
      headers: {
          'x-api-key': 'XP2lSkpg1G80lh0oHMHIe99WMVzoGL9Q4ToSU4tN'
      }
    }
    const requestBody = {
      username: user.username,
      token: JSON.parse(token)
    }
    axios.post(verifyTokenUrl, requestBody,requestConfig).then(response=>{
      
    }).catch(() => {
      resetUserSession();
    })
  },[]);

  return (
    <div className="App">
      <BrowserRouter>
        
        <div className="content">
          <Switch>
            <PublicRoute exact path="/" component={Register}/>
            <PublicRoute path="/register" component={Register}/>
            <PublicRoute path="/login" component={Login}/>
            <PrivateRoute path="/dashboard" component={Dashboard}/>
            <PrivateRoute path="/applyLoan" component={ApplyLoan}/>
            <PrivateRoute path="/update" component={Update}/>
          </Switch>
        </div>
      </BrowserRouter>
    </div>
  );
}

export default App;
